<template>
<main>
    <Header :header="content"/>
    <form action="">
        <section>
            <div class="mt-2">
            <label>نوع بسته بندی را انتخاب نمایید</label>
            <select name="" id="" v-model="val">
              <option value="0">پاکت</option>
              <option value="1">دستگاه</option>
            </select>
            </div>
        </section>
        <button class="btn btn-primary d-block  mx-auto"  @click="remTime(val)">مشاهده اطلاعات</button>

        
            <h4 style="text-align:center;margin-top:70px;color:red;">
                {{msg}}
            </h4>
    </form>
</main>
</template>

<script>
import axios from 'axios'
import Header from '../../components/Header.vue'
export default {
    name:'PackingView',
    components:{Header},
    data() {
        return {
            content:'اطلاعات بسته بندی',
            val:null,
            msg:''
        }
    },
    methods: {
        remTime(num){
            axios.get('../remtimepack.json')
            .then(({data})=>{
                this.msg=data[num];

            })
        }
    },
}
</script>

<style lang="scss" scoped>
@import '../../style/1col-form.scss'
</style>